<template>
  <v-container fluid fill-height style="padding:0;" id="line" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="linechart">绘制折线图</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <a target="_blank" href="https://github.com/qzruncode/d3-typescript">代码</a>
                    <div class="chartbox"></div>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
import * as d3 from 'd3';

export default {
  components: { Blog },
  data() {
    return {
      time: "2019-08-16",
      title: "绘制折线图",
      items: [
        {
          title: "折线图",
          target: "#linechart"
        },
        { divider: true }
      ],
    };
  },
  mounted() {
        const config = {
            chartWidth: 600,
            chartHeight: 300,
            chartMarginL: 50,
            chartMarginR: 50,
            chartMarginT: 50,
            chartMarginB: 50,
        };

        const lineChart = (Config, ChartData) => {
            const x = d3.scaleTime()
                .domain([new Date(ChartData[0].values[0].month), new Date(ChartData[0].values[ChartData[0].values.length - 1].month)])
                .range([0, config.chartWidth - config.chartMarginR - config.chartMarginL]);

            const y = d3.scaleLinear()
                .domain([0, 1.1 * d3.max(ChartData[0].values, (d) => Number(d.person))])
                .range([Config.chartHeight - Config.chartMarginT - Config.chartMarginB, 0]);

            const svg = d3.select('.chartbox')
                .append('svg')
                .attr('fill', 'none')
                .attr('stroke-linejoin', 'round')
                .attr('stroke-linecap', 'round')
                .attr('class', 'chart')
                .style('width', Config.chartWidth)
                .style('height', Config.chartHeight);

            // 折线
            const line = d3.line()
                .defined((d) => !isNaN(d.person))
                .x((d) => {
                    return x(new Date(d.month)) + Config.chartMarginL;
                })
                .y((d) => {
                    return y(d.person) + Config.chartMarginT;
                });

            svg.append('g')
                .selectAll('path')
                .data(ChartData)
                .enter()
                .append('path')
                .attr('stroke', 'steelblue')
                .attr('stroke-width', 1.5)
                .attr('d', function(d) {
                    d.line = this;
                    return line(d.values);
                });

            // 点
            const sb = d3
                .symbol()
                .size(5)
                .type(d3.symbolCircle);

            svg.append('g')
                .selectAll('path')
                .data(ChartData[0].values)
                .enter()
                .append('path')
                .attr('class', 'point')
                .attr('transform', (d, i) => {
                    return `translate(${x(new Date(d.month)) + Config.chartMarginL}, ${y(Number(d.person)) + Config.chartMarginT})`;
                })
                .attr('fill', '#fff')
                .attr('d', sb);

            // ------x轴------
            const fomateTime = d3.timeFormat('%Y-%m');
            const xg = svg
                .append('g')
                .attr('class', 'x axis')
                .attr('transform', `translate(${Config.chartMarginL}, ${Config.chartHeight - Config.chartMarginB})`);
            d3.axisBottom(x)
                .tickArguments([d3.timeMonth.every(4)])
                .tickSize(0)
                .tickPadding(8)
                .tickFormat(fomateTime)(xg);
            xg.selectAll('.tick text').attr('dx', 15);

            // -----y轴------
            const formate = d3.format('.' + d3.precisionFixed(0.1) + 'f');
            const yg = svg
                .append('g')
                .attr('class', 'y axis')
                .attr('transform', `translate(${Config.chartMarginT}, ${Config.chartMarginL})`);

            d3.axisRight(y)
                .ticks(10, formate)
                .tickSizeInner(Config.chartWidth - Config.chartMarginR - Config.chartMarginL)
                .tickPadding(8)(yg);

            // 移除y轴的竖线
            yg.select('.domain').remove();
            // 对y轴tick中的line设置线的样式
            yg.selectAll('.tick:not(:first-of-type) line')
                .attr('stroke-opacity', 0.5)
                .attr('stroke-dasharray', '2,2');
            // 设置y轴的文字的位置
            yg.selectAll('.tick text').attr('x', 4).attr('dy', -4);

            // 处理交互事件
            const voronoi = d3.voronoi()
                .x((d) => {
                    return x(new Date(d.month)) + Config.chartMarginL;
                })
                .y((d) => y(d.person) + Config.chartMarginT)
                .extent([[Config.chartMarginL, Config.chartMarginT], [Config.chartWidth - Config.chartMarginL, Config.chartHeight - Config.chartMarginT]]);


            const tooltip = svg.append('g')
                .attr('transform', 'translate(-100,-100)')
                .attr('class', 'focus');

            tooltip.append('circle')
                .style('fill', '#fff')
                .attr('r', 3.5);

            tooltip.append('text')
                .attr('class', 'texttitle')
                .attr('y', -20);

            tooltip.append('text')
                .attr('class', 'content')
                .attr('y', -10);

            const voronoiGroup = svg.append('g').attr('class', 'voronoi');
            voronoiGroup.selectAll('path')
                .data(voronoi.polygons(d3.merge(ChartData.map((d) => d.values))))
                .enter()
                .append('path')
                .attr('d', (d) => {
                    return d ? 'M' + d.join('L') + 'Z' : null;
                })
                .on('mouseover', (d) => {
                    d3.select(d.data.originLine.line).classed('city--hover', true);
                    d.data.originLine.line.parentNode.appendChild(d.data.originLine.line);
                    tooltip.attr('transform', 'translate(' + (x(new Date(d.data.month)) + Config.chartMarginL) + ',' + (y(d.data.person) + Config.chartMarginT) + ')');
                    tooltip.select('text.texttitle').text(d.data.originLine.name);
                    tooltip.select('text.content').text(d.data.month + ': ' + d.data.person + '人');
                })
                .on('mouseout', (d) => {
                    d3.select(d.data.originLine.line).classed('city--hover', false);
                    tooltip.attr('transform', 'translate(-100,-100)');
                });

        };

        d3.json('json/line.json').then((data) => {
            const d = [{
                name: '每月骑行人数',
                values: data.list,
            }];

            d.forEach((item) => {
                item.values.forEach((d) => {
                    d.originLine = item;
                });
            });

            lineChart(config, d);
        });

  },
  methods: {}
};
</script>

<style lang="scss">
#line {
  .blogcontent {
    padding-top: 20px;
  }
  .code {
    font-size: 12px;
    background-color: #f9f9f5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
  .chart {
        background:rgba(37,39,46,1);
    }

    .chart text {
        fill: white;
        font: 10px sans-serif;
        text-anchor: end;
    }

    .chart .axis text {
        font: 10px sans-serif;
    }

    .chart .axis path, .chart .axis line {
        fill: none;
        stroke: white;
        shape-rendering: crispEdges;
    }

    .voronoi path {
        fill: none;
        pointer-events: all;
    }
    
    .voronoi--show path {
        stroke: red;
        stroke-opacity: 0.2;
    }

    .city--hover {
        stroke: red;
    }

    .cities {
        fill: none;
        stroke: #aaa;
        stroke-linejoin: round;
        stroke-linecap: round;
        stroke-width: 1.5px;
    }

    .focus text {
        text-anchor: middle;
        text-shadow: 0 1px 0 #fff, 1px 0 0 #fff, 0 -1px 0 #fff, -1px 0 0 #fff;
    }
}
</style>

